.b-blog-posts {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside; }

.b-blog-posts__item {
  position: relative; }
  @media (max-width: 767px) {
    .b-blog-posts__item:nth-last-of-type(n+2) {
      margin-bottom: 23px;
      padding-bottom: 23px; } }
  @media (min-width: 768px) and (max-width: 1199px) {
    .b-blog-posts__item:nth-last-of-type(n+2) {
      margin-bottom: 43px;
      padding-bottom: 40px; } }
  @media (min-width: 1200px) {
    .b-blog-posts__item:nth-last-of-type(n+2) {
      margin-bottom: 60px;
      padding-bottom: 60px; } }
  .b-blog-posts__item:nth-last-of-type(n+2)::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    background-color: #373e42;
    opacity: 0.071;
    height: 5px; }
    @media (max-width: 1199px) {
      .b-blog-posts__item:nth-last-of-type(n+2)::after {
        width: 100%; } }
    @media (min-width: 1200px) {
      .b-blog-posts__item:nth-last-of-type(n+2)::after {
        width: 98%;
        max-width: 681px; } }

@media (max-width: 1199px) {
  .b-blog-post__header {
    margin-bottom: 5px; } }

@media (min-width: 1200px) {
  .b-blog-post__header {
    margin-bottom: 10px; } }

.b-blog-post__title {
  color: #455a64;
  font-family: "Sansus webissimo", sans-serif;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 10px; }
  @media (max-width: 767px) {
    .b-blog-post__title {
      font-size: 21px;
      line-height: 1.333; } }
  @media (min-width: 768px) and (max-width: 1199px) {
    .b-blog-post__title {
      font-size: 30px;
      line-height: 1.171; } }
  @media (min-width: 1200px) {
    .b-blog-post__title {
      font-size: 35px;
      line-height: 1.171; } }

.b-blog-post__date {
  color: rgba(78, 136, 57, 0.7);
  line-height: 2.563; }
  @media (max-width: 1199px) {
    .b-blog-post__date {
      font-size: 14px;
      line-height: 1.714; } }

.b-blog-post__main {
  margin-bottom: 20px; }
  @media (max-width: 1199px) {
    .b-blog-post__main {
      color: #566358; } }
  @media (min-width: 1200px) {
    .b-blog-post__main {
      color: #455a64; } }



