.b-content p {
  margin-top: 0; }
  @media (max-width: 767px) {
    .b-content p {
      margin-bottom: 7px;
      line-height: 1.8; } }
  @media (min-width: 768px) {
    .b-content p {
      margin-bottom: 10px;
      line-height: 1.623; } }

.b-content a {
  font-weight: 500;
  color: #6da3bd;
  text-decoration: underline; }
  .b-content a:hover, .b-content a:focus {
    text-decoration: none; }
  .b-content a:active {
    color: #4d8caa; }



